<template>
  <div class="shopSet">
    <div class="add-box">
      <el-button type="danger" @click="batchDelete" :disabled="!multipleSelection.length>0"></el-button>
      <el-button type="primary" @click="addPlace()"></el-button>
    </div>
    <el-table
      ref="multipNum_type"
      stripe
      :data="placeTypeData"
      border
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="selectType"
    >
      <el-table-column type="selection" width="55" />
      <el-table-column label="/ID" width="150">
        <template slot-scope="scope">
          <span>{{ scope.row.date }}</span>
          <span>{{ scope.row.type_id }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" width="150" />
      <el-table-column prop label="" width="150">
        <template slot-scope="scope">{{scope.row.deposit/100}}</template>
      </el-table-column>
      <el-table-column prop label="" width="150">
        <template slot-scope="scope">{{scope.row.basic_price/100}}</template>
      </el-table-column>
      <el-table-column prop="static" label="" width="150">
        <template>
          <i class="el-icon-check"></i>
        </template>
      </el-table-column>
      <el-table-column align="right" label="">
        <!-- <template slot="header" slot-scope="scope">
          <el-input v-model="search" size="mini" placeholder="" :data-str="scope" />
        </template>-->
        <template slot-scope="scope">
          <el-button size="mini" @click="updateDialog(scope.$index, JSON.parse(JSON.stringify(scope.row)),'type')"></el-button>
          <el-button size="mini" type="danger" @click="openDelDialog(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="curPage"
        :hide-on-single-page="true"
        @prev-click="prePage"
        @current-change="pageChange"
        @next-click="nextPage"
        background
        layout="prev, pager, next"
        :total="total"
      ></el-pagination>
    </div>

    <!--  -->
    <el-dialog :title="placeDialogTitle" :visible.sync="placeDialog" @close="closeDialog">
      <!--  -->
      <el-form v-show="showAddTypeBox" ref="addType" label-width="80px" class="addType">
        <el-form-item label="">
          <el-input v-model="typeForm.name" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="typeForm.basic_price" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="typeForm.deposit" />
        </el-form-item>
        <el-button type="primary" @click="addType"></el-button>
      </el-form>

      <!--  -->
      <el-form v-show="showUpdateTypeBox" ref="updateType" label-width="80px" class="updateType">
        <el-form-item label="">
          <el-input v-model="currenTypeItem.name" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="currenTypeItem.basic_price" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="currenTypeItem.deposit" />
        </el-form-item>
        <el-button type="primary" @click="updateType"></el-button>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog :title="deltitle" :visible.sync="deleteDialog" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false"> </el-button>
        <el-button type="primary" @click="deleteType"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="batchdialogVisible" width="30%">
      <span class="s-title">?</span>
      <div class="gz-name" v-for="(item, index) in nameArr" :key="index">{{item}}</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="batchdialogVisible = false"> </el-button>
        <el-button type="primary" @click="multiDelete"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ShopSet",
  props: {
    merchants: { shop_id: "", merchant_id: "" },
  },
  data() {
    return {
      msg: "",
      placeDialogTitle: "",
      deltitle: "",
      shopInfoData: {},
      placeTypeData: [
        {
          type_id: "",
          name: "",
          min_people_num: "",
          max_people_num: "",
          basic_price: "",
          deposit: "",
          sort: "",
        },
      ], // 
      typeForm: {
        type_id: "",
        name: "",
        min_people_num: "",
        max_people_num: "",
        basic_price: "",
        deposit: "",
        sort: "",
      }, // 
      currenTypeItem: {
        type_id: "",
        name: "",
        min_people_num: "",
        max_people_num: "",
        basic_price: "",
        deposit: "",
        sort: "",
      },
      placeDialog: false,
      showAddTypeBox: false,
      showUpdateTypeBox: false,
      deleteDialog: false,
      batchdialogVisible: false,
      search: "",
      curPage: 1, //
      total: 0, //
      currenDeleteRow: {},
      nameArr: [],
      multipleSelection:[],
    };
  },
  created() {
    this.getPlaceTypeInfo();
  },
  methods: {
    openDelDialog(item) {
      this.currenDeleteRow = item;
      this.deleteDialog = true;
    },
    /*  */
    selectType(val) {
      this.multipleSelection = [];
      this.nameArr = [];
      val.forEach((item, index) => {
        this.multipleSelection.push(item.type_id);
        this.nameArr.push(item.name);
      });
      console.log("nameArr:", this.nameArr);
    },
    //
    batchDelete() {
      if (this.multipleSelection) {
        this.batchdialogVisible = true;
      } else {
        this.$message({
          message: "",
          type: "warning",
        });
      }
    },
    /*  */
    getPlaceTypeInfo() {
      const data = {
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
      };
      this.api.shop.getPlaceType(data).then((res) => {
        this.placeTypeData = res.data.data;
      });
    },
    /*  */
    addPlace() {
      this.placeDialog = true;
      this.showAddTypeBox = true;
      // this.showAddAreaBox = false
      // this.showUpdateAreaBox = false
      this.showUpdateTypeBox = false;
      this.placeDialogTitle = "";
    },
    /*  */
    updateDialog(index, row) {
      this.placeDialog = true;
      this.currenTypeItem = {
        type_id: row.type_id,
        name: row.name,
        min_people_num: row.min_people_num,
        max_people_num: row.max_people_num,
        basic_price: row.basic_price / 100,
        deposit: row.deposit / 100,
        sort: row.sort,
      };
      // console.log('areaItem:', this.currenTypeItem)
      this.showUpdateTypeBox = true;
      // this.showAddAreaBox = false
      // this.showUpdateAreaBox = false
      this.showAddTypeBox = false;
    },
    /*  */
    addType() {
      console.log(this.typeForm);
      const datas = {
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
        name: this.typeForm.name,
        basic_price: parseInt(this.typeForm.basic_price) * 100,
        deposit: parseInt(this.typeForm.deposit) * 100,
      }; // 
      this.$store.dispatch("shop/createPlaceType", datas).then((res) => {
        this.getPlaceTypeInfo();
        // this.showAddAreaBox = false
        // this.showUpdateAreaBox = false
        this.showAddTypeBox = false;
        this.showUpdateTypeBox = true;
        this.placeDialog = false;
      });
    },
    /*  */
    updateType() {
      const data = {
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
        type_id: this.currenTypeItem.type_id,
        name: this.currenTypeItem.name,
        min_people_num: this.currenTypeItem.min_people_num,
        max_people_num: this.currenTypeItem.max_people_num,
        basic_price: this.currenTypeItem.basic_price * 100,
        deposit: this.currenTypeItem.deposit * 100,
        place_sort: this.currenTypeItem.place_sort,
      };
      this.$store.dispatch("shop/updatePlaceType", data).then((res) => {
        this.getPlaceTypeInfo();
        this.showAddAreaBox = true;
        this.showUpdateAreaBox = false;
        this.showAddTypeBox = false;
        this.showUpdateTypeBox = false;
        this.placeDialog = false;
      });
    },
    /*  */
    multiDelete() {
      const data = {
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
        id: this.multipleSelection,
      };
      this.$store.dispatch("shop/deletePlaceType", data).then((res) => {
        // console.log(res)
        this.getPlaceTypeInfo();
        this.batchdialogVisible = false;
      });
    },
    /*  */
    deleteType() {
      const data = {
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
        id: [this.currenDeleteRow.type_id],
      };
      this.$store.dispatch("shop/deletePlaceType", data).then((res) => {
        // console.log(res)
        this.getPlaceTypeInfo();
        this.deleteDialog = false;
      });
    },
    /*  */
    closeDialog() {
      this.showAddTypeBox = false;
      this.showUpdateTypeBox = false;
    },
    /*  */
    prePage() {
      this.curPage -= 1;
      this.getTableList(this.curPage);
    },
    /*  */
    pageChange(page) {
      this.curPage = page;
      this.getTableList(this.curPage);
    },
    /*  */
    nextPage() {
      this.curPage += 1;
      this.getTableList(this.curPage);
    },
  },
};
</script>

<style lang='scss' scoped>
.shopSet {
  .add-box {
    margin: 10px 0;
  }
  .pagination {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    padding: 15px 0;
  }
  .searchInput {
    float: right;
    width: 350px;
  }
}
</style>
